import React, { Component } from 'react';

class Questions extends Component {
  constructor (props){
    super(props)
    this.handleNameChange = this.handleNameChange.bind(this)
    this.setNames = this.setNames.bind(this);
    this.setPropsToState = this.setPropsToState.bind(this)
    this.state = {
      names:{},
      questions:[]
    }
  }

  setPropsToState(){
    let names = this.props.charactersReplacements
    let myNames = {}
    for (var name of names){
      myNames[name] = '';
    }
    this.setState({
      names : myNames,
      questions: this.props.charactersNames
    })
  }

  handleNameChange(e, id){
    let oldNames = this.state.names;
    let currentProperty = Object.keys(oldNames)[id];
    oldNames[currentProperty] = e.target.value;
  }
  setNames(event){
    event.preventDefault();
    const {namesCallback} = this.props
    let n = this.state.names
    if (namesCallback !== undefined) {
        namesCallback(n)
      }
  }

  componentDidMount(){
    this.setPropsToState()
  }

  render() {
    const {questions} = this.state
    return (
     <form className="questions" onSubmit={this.setNames}>
        {questions.map((question, index)=>{
          return <div className="field" key={index}>
            <label className="label">{question}</label>
            <div className="control">
             <input type="text" className="input" placeholder={question} onChange={(e) => this.handleNameChange(e,index)}  />
            </div>
          </div>
        })}
        <input type="submit" value="enviar" />
      </form>
    );
  }
}

export default Questions;



