import React, { Component } from 'react';
import Questions from './Questions';
import stories from './stories.json';
import reactStringReplace from 'react-string-replace';

class Story extends Component {
  constructor (props){
    super(props)
    this.handleNamesCallback = this.handleNamesCallback.bind(this)
    this.setPropsToState = this.setPropsToState.bind(this)
    this.state = {
      story:null,
      ready:false
    }
  }
  
  filterValue(obj, key, value) {
    return obj.find(function(v){ return v[key] === value});
  }

  setPropsToState(){
    const currentId = this.props.id;
    const currentStory = stories.find(x => x.id === currentId)
    this.setState({
      story : currentStory,
    })
   }

   
   cleanStory(){
     this.setState({
      story:null,
      ready:false
     })
   }
  updateNames(event, story){
    event.preventDefault();
  }

  handleNamesCallback(names){
    let oldStory = this.state.story
    oldStory['n'] = names;
    let oldText = this.state.story.text

    for(var replacer of oldStory.charactersReplacements){
        var name = oldStory.n[replacer]

        let testVar = reactStringReplace(oldText, replacer, (match, i) => (
            name
        ));

        testVar = testVar.join('');
        oldText = testVar
    }
    
    oldStory.text = oldText

    this.setState({
      story: oldStory,
      ready:true
    })
    
  }

  componentDidMount(){
    this.setPropsToState()
  }

  render() {
    const {story} = this.state
    return (
     <div className="Story">
      {story
      ? 
        <div>
          <h2 className="title is-4">{story.title}</h2>
          { !this.state.ready ? <Questions 
              charactersNames={this.state.story.charactersNames} 
              charactersReplacements={this.state.story.charactersReplacements} 
              namesCallback={this.handleNamesCallback}
              n={this.state.story.n} /> : ''}
          <div className={!this.state.ready ? 'beHidden' : 'beVisible' }>
            <p dangerouslySetInnerHTML={{__html: story.text}}></p>
          </div>
          <a href="/" onClick={this.cleanStory}>volveral comienzo</a>
        </div>: ''}
      </div> 
    );
  }
}

export default Story;



