import React, { Component } from 'react';
import { BrowserRouter as Router, NavLink} from 'react-router-dom';
import Route from 'react-router-dom/Route'
import Story from './Story';
import './assets/css/App.css';
import stories from './stories.json';


class App extends Component {
  constructor (props) {
    super(props)
    this.state = {
      storiesList: [],
      selectedStory:{}
    }
  }
  componentDidMount () {
    this.setState({
      storiesList: stories,
      selectedStory:{}
    })
  }
  render() {  
    const {storiesList} = this.state
    return (
        <Router>
          <div className="App container">
            <div className="box main columns is-multiline is-centered">
            <Route path="/" exact strict render={() => {
                        return (<div>
                          <h4 className="title is-4 has-text-centered">este es un titulo de la pagina</h4>
                          <p className="subtitle is-6 is-spaced has-text-centered">Bienvenido a la pagina de historias, elige cual quieres leer</p>
                          <ul>
                            {storiesList.map((story, index)=>{
                              return <li><NavLink to={'/story/'+story.id} exact>{story.title}</NavLink></li>
                            })}
                          </ul>
                        </div>);
                      }
            }/>
            <Route path="/story/:id" exact strict render={({match})=>(
                  <Story id={match.params.id} />
            )}/>
            </div>
          </div>
        </Router>
    );
  }
}

export default App;



